<template>
  <el-dialog class="add-together-popup" title="新增" :visible="showDialog" @close="onClose()" width="1000">
    <div v-if="step == 1">
      <el-form  label-position="left">
      <el-form-item>
            <el-input v-model="formItem.title" placeholder="請輸入湊團物品名稱"></el-input>
        </el-form-item>
        <el-form-item label="組合">
            <el-select v-model="formItem.groupName" placeholder="请選擇組合">
                <el-option v-for="(member,i) in groups" :label="member"  :value="member" :key="i"></el-option>
            </el-select>
        </el-form-item>
         <el-form-item>
            <el-input v-model="formItem.desc" placeholder="描述"></el-input>
        </el-form-item>
        <el-upload
            action="https://jsonplaceholder.typicode.com/posts/"
            list-type="picture-card">
        <i class="el-icon-plus"></i>
        </el-upload>
        <el-form-item>
            <el-date-picker
              v-model="formItem.expiredDate"
              type="date"
              placeholder="选择截止日期">
            </el-date-picker>
        </el-form-item>
        <div class="item-detail" v-for="(item,i) in formItem.itemList" :key="i">
          <el-form-item
          >
            <el-input v-model="item.itemName" placeholder="请輸入項目名稱"></el-input>
          </el-form-item>
          <el-form-item  class="item-detail"
          >
            <el-select v-model="item.itemNum" placeholder="请選擇數量">
                <el-option v-for="(num,i) in 20" :value="num" :key="i"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item  class="item-detail"
          >
            <el-input v-model="item.price" placeholder="请輸入單項價錢"></el-input>
          </el-form-item>
          <el-button @click="removeItem(i)">刪除</el-button>
        </div>
        <el-button @click="addItem()">新增</el-button>
        </el-form>
    </div>
     <div class="itemList" v-else>
       <p>確認</p>
       <p>{{formItem.title}}</p>
       <p>{{formItem.GroupName}}</p>
       <p>{{formItem.desc}}</p>
       <p>{{formItem.expiredDate}}</p>
       <p v-for="(item,i) in formItem.itemList" :key="i">{{item.itemName}}{{item.itemNum}} {{item.itemPrice}}</p>
    </div>
    <div slot="footer" class="dialog-footer">
              <el-button @click="onClose()">取 消</el-button>
              <el-button type="primary" @click="onSubmit()">确 定</el-button>
          </div>
        </el-dialog>
</template>

<script>
import { mapState } from 'vuex'
export default {
   props: {
        showDialog: {
            type: Boolean,
            require: true
        }
    },
    data(){
      return {
        step: 1,
        formItem:{
          title:"",
          groupName:"",
          desc:"",
          expiredDate:"",
          itemList:[{
            itemName:"",
            itemNum:null,
            price:null,
          }]
        },
      }
    }, 
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
        }),
    },
    methods: {
      onClose(){
            this.$emit('update:showDialog', false)
        },
        removeItem(index) {
          this.formItem.itemList.splice(index, 1)
      },
      onSubmit(){
        if (this.step == 1){
          this.step = 2
        } else {
          this.$emit('update:showDialog', false)
        }
            
        },
      addItem() {
        this.formItem.itemList.push({
          itemName:"",
          itemNum:0,
          key:Date.now()
        });
        console.log(this.formItem)
      }
    }

}
</script>

<style lang="scss" scoped>
.add-together-popup {
  .item-detail {
      display: flex;
    
  }
}

</style>